.main_banner {
  color: lightgrey;
  height: 445px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  margin: auto;
}
.main_banner .banner_img img {
  height: 300px;
  border-radius: 5px;
}
.main_banner .banner_info {
  min-width: 300px;
}
.main_banner .banner_info .banner_info_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main_banner .banner_info .banner_info_item h3,
.main_banner .banner_info .banner_info_item p {
  margin: 0;
  font-size: 24px;
}
.main_banner .banner_info .banner_info_item h3 {
  margin-right: 20px;
}
.main_banner .banner_info .banner_info_item p {
  color: #f0c129;
}
.main_banner .banner_info .banner_info_item p svg {
  color: #f0c129;
  margin-right: 10px;
}

.banner_info_description {
  max-width: 425px;
  line-height: 28px;
}

.banner_info_genres,
.banner_info_description {
  font-size: 16px;
}

.banner_info_genres {
  margin-bottom: 15px;
  padding: 0;
}

