.ant-layout-sider,
.ant-layout-sider-dark {
  margin-right: 20px;
  background: transparent !important;
}

.movie_container .movie_poster {
  width: 100%;
}
.movie_container .movie_poster img {
  display: block;
  width: 100%;
}
.movie_container .movie_description .movie_description_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.movie_container .movie_description .movie_description_title h3 {
  padding: 5px;
  margin: 0;
  font-size: 28px;
  width: fit-content;
  color: lightgrey;
}
.movie_container .movie_description .movie_description_title span {
  padding: 10px;
  color: lightgrey;
}
.movie_container .movie_description .movie_description_title span:not(:last-of-type) {
  margin-right: 15px;
}
.movie_container .movie_description .movie_description_tagline {
  font-size: 18px;
}
.movie_container .movie_description ul {
  list-style: none;
  padding: 5px;
}
.movie_container .movie_description ul li {
  line-height: 32px;
  font-size: 16px;
  width: fit-content;
  color: lightgrey;
}
.movie_container .movie_description ul li span:first-of-type {
  font-weight: bold;
}
.movie_container .movie_description .movie_description_overview {
  line-height: 26px;
  margin-bottom: 20px;
  font-size: 16px;
}
.movie_container .movie_trailer {
  width: 100%;
  background: #151a20;
  height: 100%;
}

